import { lazy } from 'react';

// LANDING SECTION
const LandginHome = lazy(() => import('./landing section/Home'));
const Signin = lazy(() => import('./landing section/Auths/Signin'));
const ResetPassword = lazy(() => import('./landing section/Auths/ResetPassword'));
const Activation = lazy(() => import('./landing section/Auths/Activation'));

// ADMIN SECTION
const AdminDashboard = lazy(() => import('./admin section/Dashboard'));

const AdminUsers = lazy(() => import('./admin section/hr/Users'));
const AdminCompanies = lazy(() => import('./admin section/hr/Companies'));
const AdminRoles = lazy(() => import('./admin section/hr/Roles'));

const AdminPackages = lazy(() => import('./admin section/packages/Packages'));
const AdminPromotions = lazy(() => import('./admin section/packages/Promotions'));
const AdminSubscriptions = lazy(() => import('./admin section/packages/Subscriptions'));

const AdminAbout = lazy(() => import('./admin section/settings/About'));
const AdminContacts = lazy(() => import('./admin section/settings/Contacts'));
const AdminTerms = lazy(() => import('./admin section/settings/Terms'));
const AdminPrivacy = lazy(() => import('./admin section/settings/Privacy'));
const AdminFAQ = lazy(() => import('./admin section/settings/FAQ'));
const AdminComplaints = lazy(() => import('./admin section/settings/Complaints'));

const AdminCategories = lazy(() => import('./admin section/services/Categories'));
const AdminBrands = lazy(() => import('./admin section/services/Brands'));
const AdminProducts = lazy(() => import('./admin section/services/Products'));
const AdminOrders = lazy(() => import('./admin section/services/Orders'));
const AdminOffers = lazy(() => import('./admin section/services/Offers'));

const AdminPayments = lazy(() => import('./admin section/reports/Payments'));




export {
    LandginHome,
    Signin, ResetPassword, Activation,
    AdminDashboard,

    AdminUsers, AdminCompanies, AdminRoles,
    AdminPackages, AdminPromotions, AdminSubscriptions,
    AdminAbout, AdminContacts, AdminTerms, AdminPrivacy, AdminFAQ, AdminComplaints,
    AdminCategories, AdminBrands, AdminProducts, AdminOrders, AdminOffers,
    AdminPayments,

};